<template>
  <div
    class="vue--picture-cropper__wrap"
    :style="boxStyle"
  >
    <img
      class="vue--picture-cropper__img"
      :src="img"
    />
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue'
import Cropper from 'cropperjs'
import 'cropperjs/dist/cropper.css'

/** 
 * api
 */
export let cropper: any = null;

/** 
 * 
 */
const VuePictureCropper = defineComponent({
  name: 'VuePictureCropper',
  props: {
    boxStyle: {
      type: Object,
      required: false
    },
    img: String,
    options: {
      type: Object,
      required: false,
      default: () => ({})
    }
  },
  data () {
    return {
      cropper: null
    }
  },
  watch: {
    /** 
     * 
     * 
     */
    img (newVal, oldVal) {

      // 
      if ( !this.cropper ) {
        this.init();
        return false;
      }

      // 
      try {
        this.cropper.replace(this.img);
      } catch (e) {
        console.log(e);
      }

    }
  },
  beforeUnmount () {

    /** 
     * 
     */
    if ( this.cropper ) {
      this.cropper.destroy();
    }

  },
  mounted () {
    this.init();
  },
  methods: {

    /** 
     * 
     */
    async init () {
      // 
      await this.$nextTick();
      
      // DOM
      const CHECK: any = setInterval( () => {

        // DOM
        const IMG_DOM: any = document.querySelector('.vue--picture-cropper__img');

        // DOM
        if ( IMG_DOM ) {

          // Vue
          try {
            this.cropper = new Cropper(IMG_DOM, this.options);

            // 
            clearInterval(CHECK);

            // 
            cropper = this.cropper;

          } catch (e) {
            console.log(e);
          }
        }
      }, 10);
    }

  }
})

export default VuePictureCropper
</script>

<style>
.vue--picture-cropper__img {
  display: block;
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}
</style>